












import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  props: {
    max: {
      type: Number,
      default: 5,
    },
    value: {
      type: Number,
      default: 0,
    },
    barColor: {
      type: String,
      default: 'linear-gradient(90deg, #171c60 0%, #009ee8 100%)',
    },
    highThreshold: {
      type: Number,
      default: Infinity,
    },
    highBarColor: {
      type: String,
      default: '#ff5a5f',
    },
  },
  setup(props, context) {
    const isHigh = props.value >= props.highThreshold
    const background = isHigh ? props.highBarColor : props.barColor
    const width = `${(props.value / props.max) * 100}%`
    return {
      background,
      width,
      context,
    }
  },
})
